<template>
    <div class="countDown"><!---->
        <span class="character"></span>
        <span>
        <i>{{hs}}</i>:
        <i>{{ms}}</i>:
        <i>{{ss}}</i>
      </span>
    </div>
</template>

<script>
    export default {
        name: "countDown",
        data() {
            return {
                timeName: null,//
                /*========*/
                hs: 0,
                ms: 0,
                ss: 0,
                lDur: null,//  
                nowTime: null,
                starTime:null,
                deadline:null
            }
        },
        props: {
            examInfo:Object
        },
        created() {
            //    1000
            this.nowTime = parseInt(this.examInfo.nowTime/1000);
            this.starTime = parseInt(this.examInfo.starTime/1000);
            if (this.examInfo.deadline){
                this.deadline = parseInt(this.examInfo.deadline/1000);
            }
            this.duration = this.examInfo.duration*60;//  60
        },
        mounted(){
            // 
            clearInterval(this.timeName);
            let duration = this.duration;
            let nowTime = this.nowTime;
            let starTime = this.starTime;
            //  (--)
            this.lDur = parseInt(duration - (nowTime-starTime));
            let that = this;
            // 
            that.hs = ('0' + Math.floor(that.lDur / 3600)).slice(-2);
            that.ms = ('0' + Math.floor(that.lDur / 60 % 60)).slice(-2);
            that.ss = ('0' + Math.floor(that.lDur % 60 % 60)).slice(-2);

            // 
            that.timeName = setInterval(function () {
                that.hs = ('0' + Math.floor(that.lDur / 3600)).slice(-2);
                that.ms = ('0' + Math.floor(that.lDur / 60 % 60)).slice(-2);
                that.ss = ('0' + Math.floor(that.lDur % 60 % 60)).slice(-2);

                that.nowTime++;
                that.lDur--;
            }, 1000)
        },
        methods: {
            clearTime() { // 
                clearInterval(this.timeName);
            },
            removeSession(path) { // session
                // sessionStorage.removeItem('lDur' + this.pid);
                this.$router.replace({path: path}); // session
            }
        },
        watch: {
          lDur: function (val) { // lDur0  
            if ((this.deadline && this.nowTime > this.deadline) || val < 0 ) {
              //  
              this.$emit('autoSubmit');
            }
          }
        },
        beforeDestroy () {
          this.clearTime()
        }
    }
</script>

<style scoped>
    .countDown img {
        vertical-align: middle;
        margin: 0 10px 5px 0;
    }

    .countDown .character {
        color: #e71517;
        margin-right: 10px;
    }

    .countDown span i {
        display: inline-block;
        width: 25px;
        height: 25px;
        border: 1px solid #E7E3E7;
        text-align: center;
        line-height: 27px;
        background-color: #fff;
    }

    .countDown span i:nth-child(1),
    .countDown span i:nth-child(2) {
        margin-right: 3px;
        text-align: center;
        line-height: 27px;
    }
</style>
